import React from "react";
import "./Login.css";
import { Link, Redirect } from "react-router-dom";

function Login(props) {
  React.useEffect(() => {
    props.setIsHeaderVisible(false);
  }, []);

  const passwordRef = React.useRef('');
  const [isBtnDisabled, setIsBtnDisabled] = React.useState(true);

  function handleChange(e) {
    setIsBtnDisabled(!e.target.value);
  }

  function handleSubmit(e) {
    e.preventDefault();

    if (!passwordRef.current.value) {
      return;
    }
    props.onLogin(passwordRef.current.value);
  }

  return (
    <section className="login">
      <div className='login__header login__header_underlined'>
        <p className='login__title'> </p>
      </div>
      <form
        method="get"
        action="index.html"
        name="password"
        className='login__form'
        onSubmit={handleSubmit}
        noValidate
        autoComplete="off"
        onChange={handleChange}
      >
        <label className="login__form-field" htmlFor="password">
          <input name="password"
            type="password"
            ref={passwordRef}
            placeholder="" id="password"
            className="login__form-input login__form-input_phone"
          ></input>
        </label>
        <button type="submit" disabled={isBtnDisabled} className={`login__form-submit ${isBtnDisabled ? 'login__form-submit_disabled' : ''}`}></button>
      </form>
    </section>
  );
}

export default Login;
